import React, {Component, Fragment} from 'react'
import '../index.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.js';
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {
    get_list_order,
    get_search_list_order,
    add_order,
    update_order,
} from './ducks'
import $ from "jquery";

$('#interactOrder').modal('toggle');
$('#interactJob').modal('toggle');

class Order extends Component {
    state = {
        popupStateOrder: "",
        popupStateJob: "",
        editObject: {},
        orderName: "",
        jobName: "",
        jobDescription: "",
        jobServiceInterval: 0,
        jobDuration: 0
    };

    componentDidMount() {
        this.props.get_list_order();
    }

    /*Функция для поиска регламентных работ (фильтрование списков регламентных работ)*/
    search_order = event => {
        const value = event.target.value.toLowerCase();
        let list = this.props.list_order.map(a => ({...a}));
        const filterList = list.filter(order => {
            order.jobs = order.jobs.filter(job => {
                return job.ref.name.toLowerCase().includes(value);
            });
            return order;
        });
        this.props.get_search_list_order(filterList);
    };

    handleSelectAddOrder = () => {
        this.setState({
            popupStateOrder: "add",
            orderName: "",
        });
    };
    handleSelectEditOrder = (name, object) => {
        this.setState({
            popupStateOrder: "edit",
            editObject: object,
            orderName: name
        });
    };
    handleSelectAddJob = () => {
        this.setState({
            popupStateJob: "add",
            jobName: "",
        });
    };

    handleChangeNameOrder = event => {
        event.preventDefault();
        this.setState({orderName: event.target.value});
    };

    handleSelectEditJob = (name, description, serviceInteval, duration, object) => {
        this.setState({
            popupStateJob: "edit",
            editObject: object,
            jobName: name,
            jobDescription: description,
            jobServiceInterval: serviceInteval,
            jobDuration: duration
        });
    };

    handleChangeNameJob = event => {
        event.preventDefault();
        this.setState({jobName: event.target.value});
    };
    handleChangeServiceIntervalJob = event => {
        event.preventDefault();
        this.setState({jobServiceInterval: event.target.value});
    };
    handleChangeDurationJob = event => {
        event.preventDefault();
        this.setState({jobDuration: event.target.value});
    };
    handleChangeDescriptionJob = event => {
        event.preventDefault();
        this.setState({jobDescription: event.target.value});
    };

    handleSubmitAddOrder = event => {
        event.preventDefault();
        this.props.add_order(this.state.orderName);
    };
    handleSubmitEditOrder = event => {
        event.preventDefault();
        this.props.update_order(this.state.orderName, this.state.editObject);
    };

    render() {
        const list_order = Object.values(this.props.search_list_order);

        return (
            <Fragment>
                <div className="row">
                    <form className="col-sm-8 col-xl-8 mt-3 pr-0">
                        <input className="form-control" type="search" placeholder="Поиск по регламентам"
                               aria-label="Search" onChange={this.search_order}/>
                    </form>
                    <div className="col-sm-2 col-xl-2 mt-3 pr-0">
                        <button className="btn btn-outline-danger col-12" data-toggle="modal"
                                data-target="#interactOrder" onClick={this.handleSelectAddOrder}>Добавить
                            регламент
                        </button>
                    </div>
                    <div className="col-sm-2 col-xl-2 mt-3">
                        <button className="btn btn-outline-danger col-12" data-toggle="modal"
                                data-target="#interactJob" onClick={this.handleSelectAddJob}>Добавить
                            тип работ
                        </button>
                    </div>
                </div>
                <table className="table mt-3 text-center">
                    <thead className="thead-light">
                    <tr className="d-flex">
                        <th className="col">Название</th>
                        <th className="col">Описание</th>
                        <th className="col">Периодичность</th>
                        <th className="col">Длительность</th>
                        <th className="col-1"/>
                    </tr>
                    </thead>
                    <tbody>
                    {
                        list_order.map((order, i) => {

                            let headerOrder = <tr key={i.toString()} className="d-flex table-secondary">
                                <td className="col">{order.name}</td>
                                <td className="col"/>
                                <td className="col"/>
                                <td className="col">

                                </td>
                                <td className="col-1">
                                    <button className="font-awesome-button" data-toggle="modal"
                                            data-target="#interactOrder"
                                            onClick={() => this.handleSelectEditOrder(order.name, order)}>
                                        <i className="fas fa-pencil-alt"/>
                                    </button>
                                    <button type="button" className="btn delete_button">
                                        <i className="fas fa-trash-alt"/>
                                    </button>
                                </td>
                            </tr>;
                            return (
                                [headerOrder, order.jobs.map((job, j) => {
                                    job = job.ref;
                                    return (
                                        <tr key={i.toString() + j.toString()} className="d-flex">
                                            <td className="col">{job.name}</td>
                                            <td className="col">{job.description}</td>
                                            <td className="col">{job.serviceInterval}</td>
                                            <td className="col">{job.duration}</td>
                                            <td className="col-1">
                                                <button className="font-awesome-button" data-toggle="modal"
                                                        data-target="#interactJob"
                                                        onClick={() => this.handleSelectEditJob(job.name, job.description, job.serviceInterval, job.duration, job)}>
                                                    <i className="fas fa-pencil-alt"/>
                                                </button>
                                                <button type="button" className="btn delete_button">
                                                    <i className="fas fa-trash-alt"/>
                                                </button>
                                            </td>
                                        </tr>
                                    )
                                })]
                            )
                        })
                    }
                    </tbody>
                </table>

                {/*Модальное окно добавления/редактирования регламента*/}

                <div id="interactOrder" className="modal fade" role="dialog">
                    <div className="modal-dialog">
                        <div className="modal-content">
                            <form id="interactOrder"
                                  onSubmit={this.state.popupStateOrder === "add" ? this.handleSubmitAddOrder : this.handleSubmitEditOrder}>
                                <div className="modal-header">
                                    <h4 className="modal-title">{this.state.popupStateOrder === "add" ? 'Создание' : 'Редактирование'} регламента</h4>
                                    <button type="button" className="close" data-dismiss="modal">&times;</button>
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <label htmlFor="interactOrderName">Название</label>
                                    <input className="form-control" id="interactLocationName" type="text"
                                           placeholder="Введите название регламента"
                                           onChange={this.handleChangeNameOrder}
                                           required
                                           value={this.state.orderName}/>
                                </div>
                                <div className="modal-footer">
                                    <button type="submit" className="btn btn-outline-danger">
                                        {this.state.popupStateOrder === "add" ? 'Добавить' : 'Изменить'}
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>

                {/*Модальное окно добавления/редактирования регламентной работы*/}

                <div id="interactJob" className="modal fade" role="dialog">
                    <div className="modal-dialog">
                        <div className="modal-content">
                            <form id="interactJob"
                                  onSubmit={this.state.popupStateJob === "add" ? this.handleSubmitAddLocation : this.handleSubmitEditLocation}>
                                <div className="modal-header">
                                    <h4 className="modal-title">{this.state.popupStateJob === "add" ? 'Создание' : 'Редактирование'} типа
                                        работы</h4>
                                    <button type="button" className="close" data-dismiss="modal">&times;</button>
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <label htmlFor="interactJobName">Название</label>
                                    <input className="form-control" id="interactLocationName" type="text"
                                           placeholder="Введите название типа работы"
                                           onChange={this.handleChangeNameJob}
                                           required
                                           value={this.state.jobName}/>
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <label htmlFor="interactJobName">Периодичность</label>
                                    <input className="form-control" id="interactLocationName" type="text"
                                           placeholder="Введите периодичность типа работы"
                                           onChange={this.handleChangeServiceIntervalJob}
                                           required
                                           value={this.state.jobServiceInterval}/>
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <label htmlFor="interactJobName">Длительность</label>
                                    <input className="form-control" id="interactLocationName" type="text"
                                           placeholder="Введите длительность типа работы"
                                           onChange={this.handleChangeDurationJob}
                                           required
                                           value={this.state.jobDuration}/>
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <label htmlFor="interactJobDescription">Описание</label>
                                    <textarea className="form-control" id="interactLocationName"
                                              placeholder="Введите описание типа работы"
                                              onChange={this.handleChangeDescriptionJob}
                                              required
                                              value={this.state.jobDescription}/>
                                </div>
                                <div className="modal-footer">
                                    <button type="submit" className="btn btn-outline-danger">
                                        {this.state.popupStateJob === "add" ? 'Добавить' : 'Изменить'}
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}

const mapStateToProps = ({listOrder}) => ({
    list_order: listOrder.list_order,
    search_list_order: listOrder.search_list_order
});

const mapDispatchToProps = dispatch =>
    bindActionCreators(
        {
            get_list_order,
            get_search_list_order,
            add_order,
            update_order,
        },
        dispatch
    );

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Order)