import {
    UPDATE_MAINTENANCE_PATH,
    ADD_STATE_MAINTENANCE_PATH,
    ALL_MAINTENANCE_PATH,
    GET_SHIFT_BY_DATE,
    IP_HOST,
    CREATE_MAINTENANCE_PATH,
    ALL_SHIFTS_PATH,
    ADD_SHIFT_BY_DATE,
    DELETE_MAINTENANCE_PATH,
    RETRY_MAINTENANCE_PATH, templateMaintenance
} from "../../commonComponents/Const";
import * as allConst from '../../commonComponents/Const';
import $ from "jquery";

export const GET_LIST_MAINTENANCE = 'GET_LIST_MAINTENANCE';
export const ADD_MAINTENANCE = 'ADD_MAINTENANCE';
export const DELETE_MAINTENANCE = 'DELETE_MAINTENANCE';
export const EDIT_MAINTENANCE = 'EDIT_MAINTENANCE';
export const CLOSE_MAINTENANCE = 'CLOSE_MAINTENANCE';
export const RETRY_MAINTENANCE = 'RETRY_MAINTENANCE';
export const ADD_SHIFT = 'ADD_SHIFT';
export const GET_LIST_SHIFTS = 'GET_LIST_SHIFTS';

const initialState = {
    list_maintenance: [],
    list_shifts: []
};

/*reducers*/
export default (state = initialState, action) => {
    switch (action.type) {
        case GET_LIST_MAINTENANCE:
            return {
                ...state,
                list_maintenance: action.list_maintenance,
            };
        case ADD_MAINTENANCE:
            return {
                ...state,
                list_maintenance: [...state.list_maintenance, action.new_maintenance]
            };
        case DELETE_MAINTENANCE:

            let index1 = state.list_maintenance.map(maintenance => maintenance.oid).indexOf(action.id);
            state.list_maintenance.splice(index1, 1);
            return {
                ...state,
                list_maintenance: [...state.list_maintenance],
            };
        case CLOSE_MAINTENANCE:

            let index2 = state.list_maintenance.map(maintenance => maintenance.oid).indexOf(action.id);
            state.list_maintenance[index2].maintenanceState = 12;
            return {
                ...state,
                list_maintenance: [...state.list_maintenance],
            };
        case RETRY_MAINTENANCE:
            let new_oid = state.list_maintenance[state.list_maintenance.length - 1].oid + 1;
            let index3 = state.list_maintenance.map(maintenance => maintenance.oid).indexOf(action.id);
            state.list_maintenance[index3].maintenanceState = 10;
            state.list_maintenance[index3].maintenanceType = 4;
            state.list_maintenance[index3].child = {
                oid: new_oid
            };

            let retry_maintenance = Object.assign({}, state.list_maintenance[index3]);
            retry_maintenance.oid = new_oid;
            retry_maintenance.maintenanceState = 2;
            retry_maintenance.parent = {
                oid: state.list_maintenance[index3].oid
            };
            retry_maintenance.visitDate = {
                timeInMS: 0
            };

            state.list_maintenance.push(retry_maintenance);
            return {
                ...state,
                list_maintenance: [...state.list_maintenance],
            };
        case EDIT_MAINTENANCE:
            state.list_maintenance.map((maintenance, i) => {
                if (maintenance.oid === action.new_maintenance.oid) {
                    state.list_maintenance[i] = action.new_maintenance;
                }
            });
            return {
                ...state,
                list_maintenance: [...state.list_maintenance]
            };
        case GET_LIST_SHIFTS:
            return {
                ...state,
                list_shifts: action.list_shifts,
            };
        case ADD_SHIFT:
            return {
                ...state,
                list_shifts: [...state.list_shifts, action.new_shift]
            };
        default:
            return state
    }
}

/*actions*/
export const get_list_maintenance = () => {

    return dispatch => fetch(`${IP_HOST}${ALL_MAINTENANCE_PATH}`, {
        method: "GET",
        headers: {'SessionToken': allConst.getCurrentUser().sessionToken}
    }).then(function (response) {
        if (response.status === 401) {
            document.location.href = "/";
        }
        return response.json()
    }).then(data => {

        dispatch({
            type: GET_LIST_MAINTENANCE,
            list_maintenance: data
        });

        console.log("Список заявок получен \n", data);
    }).catch(function (error) {
        console.log("Список заявок не получен\n", error.message);
    });
};

export const add_maintenance = (facility, type) => {
    return dispatch => fetch(`${IP_HOST}${CREATE_MAINTENANCE_PATH}?id=${facility.id}&type=${type}&state=2`, {
        method: "POST",
        headers: {'SessionToken': allConst.getCurrentUser().sessionToken},
    }).then(function (response) {
        if (response.status === 401) {
            document.location.href = "/";
        }
        return response.json()
    }).then(data => {
        $(function () {
            $('#interactMaintenance').modal('toggle');
        });
        let new_data = {
            oid: data,
            maintenanceState: 2,
            maintenanceType: 2,
            facility: {
                oid: facility.id,
                operation: 0,
                ref: facility.object
            },
        };
        let body = Object.assign(templateMaintenance, new_data);

        dispatch({
            type: ADD_MAINTENANCE,
            new_maintenance: body
        });

        console.log("Заявка добавлена \n", data, body);
    }).catch(function (error) {
        console.log("Заявка не добавлена\n", error.message);
    });
};

