export const move_maintenance_to_technician = (id, dayTime, dragTime, itemObject) => {

    // Получение смены по дате
    return dispatch => fetch(`${IP_HOST}${GET_SHIFT_BY_DATE}&technicianid=${id}&timeInMS=${dayTime}`, {
        method: "GET",
        headers: {'SessionToken': allConst.getCurrentUser().sessionToken}
    }).then(function (response) {
        if (response.status === 401) {
            document.location.href = "/";
        }
        return response.json()
    }).then(data => {
        let body = Object.assign({}, itemObject);
        body.duration = body.maintenanceState === 2 || body.maintenanceState === 5 || body.maintenanceState === 9 ? 60 : body.duration;
        body.visitDate = {
            timeInMS: dragTime
        };
        body.shift = {
            oid: data.oid // назначение на смену
        };
        body.maintenanceState = 4;

        // Установка времени начала и длительности заявки
        fetch(`${IP_HOST}${UPDATE_MAINTENANCE_PATH}`, {
            method: "POST",
            headers: {'SessionToken': allConst.getCurrentUser().sessionToken},
            body: JSON.stringify(body)
        }).then(function (response) {
            if (response.status === 401) {
                document.location.href = "/";
            }
            return response.json()
        }).then(d => {

            dispatch({
                type: EDIT_MAINTENANCE,
                new_maintenance: body
            });

            console.log("Время начала и длительность установлены\n", d);
        }).catch(function (error) {
            console.log("Время начала и длительность не установлены'\n", error.message);
        });
        console.log("Текущая смена получена, заявка назначена\n", data);
    }).catch(function (error) {
        console.log("Текущая смена не получена, заявка не назначена\n", error.message);
    });
};

export const resize_maintenance_to_technician = (item, duration, timeStart) => {

    item.duration = duration;
    item.visitDate = {
        timeInMS: timeStart
    };

    return dispatch => fetch(`${IP_HOST}${UPDATE_MAINTENANCE_PATH}`, {
        method: "POST",
        headers: {'SessionToken': allConst.getCurrentUser().sessionToken},
        body: JSON.stringify(item)
    }).then(function (response) {
        if (response.status === 401) {
            document.location.href = "/";
        }
        return response.json()
    }).then(data => {

        dispatch({
            type: EDIT_MAINTENANCE,
            new_maintenance: item
        });

        console.log("Время начала и длительность установлены\n", data);
    }).catch(function (error) {
        console.log("Время начала и длительность не установлены'\n", error.message);
    });
};

