import React, {Component, Fragment} from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.js';
import {
    get_list_facility,
    get_search_list_facility,
    reverse_list_facility,
    add_facility,
    edit_facility,
    delete_facility,
    get_list_contractor,
    add_technician_to_facility,
} from './ducks'
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {facilityState, getFIO} from "../../commonComponents/Const";
import Select from 'react-select';
import {get_list_locations} from "../locations/ducks";
import {get_list_technicians} from "../technicians/ducks";
import {get_list_order} from "../order/ducks";

class Facility extends Component {
    state = {
        id: "",
        name: "",
        contractor: {id: 0, value: '', label: 'Выберите контрагент', object: {oid: 0}},
        location: {id: 0, value: '', label: 'Выберите локацию', object: {oid: 0}},
        address: "",
        technician: {id: 0, value: '', label: 'Выберите техника', object: {oid: 0}},
        order: {object: {oid: 0}},
        popupState: "",
        posEditElement: null,
    };

    componentDidMount() {
        this.props.get_list_facility();
        this.props.get_list_locations();
        this.props.get_list_technicians();
        this.props.get_list_contractor();
        this.props.get_list_order();
    }

    /*Функция для поиска объектов (фильтрование списков объектов)*/
    search_facility = event => {
        const value = event.target.value.toLowerCase();

        const filterList = this.props.list_facility.filter(facility => {
            return facility.name.toLowerCase().includes(value);
        });
        if (!this.props.sortUp_facility) {
            filterList.reverse();
        }
        this.props.get_search_list_facility(filterList);
    };
    handleSelectAddFacility = () => {
        this.setState({
            popupState: "add",
            id: "",
            name: "",
            contractor: {id: 0, value: '', label: 'Выберите контрагент', object: {oid: 0}},
            location: {id: 0, value: '', label: 'Выберите локацию', object: {oid: 0}},
            address: "",
            technician: {id: 0, value: '', label: 'Выберите техника', object: {oid: 0}},
            order: {object: {oid: 0}}
        });
    };
    handleSelectEditFacility = (pos, identifier, name, contractor, address, location, technician) => {
        this.setState({
            popupState: "edit",
            posEditElement: pos,
            id: identifier,
            name: name,
            contractor: contractor,
            address: address,
            technician: technician,
            location: location,
        })
    };
    handleSubmitAddFacility = event => {
        event.preventDefault();

        if (this.state.location.object.oid !== 0 && this.state.contractor.object.oid !== 0 && this.state.technician.object.oid !== 0) {
            let data = {identifier: this.state.id, name: this.state.name};
            data["zone"] = {
                oid: this.state.location.object.oid,
                ref: this.state.location.object
            };
            delete data.zone.ref.oid;

            data["contractor"] = {
                oid: this.state.contractor.object.oid,
                ref: this.state.contractor.object
            };
            delete data.contractor.ref.oid;

            data["technician"] = {
                oid: this.state.technician.object.oid,
                ref: this.state.technician.object
            };
            delete data.technician.ref.oid;

            this.props.add_facility(data);

        }
    };
    handleSubmitEditFacility = event => {
        event.preventDefault();
        const data = this.props.list_facility[this.state.posEditElement];
        if (this.state.location.object.oid !== 0 && this.state.contractor.object.oid !== 0 && this.state.technician.object.oid !== 0) {
            data.identifier = this.state.id;
            data.name = this.state.name;

            data.zone.oid = this.state.location.object.oid ? this.state.location.object.oid : data.zone.oid;
            data.zone.ref = this.state.location.object;
            delete data.zone.ref.oid;

            data.contractor.oid = this.state.contractor.object.oid ? this.state.contractor.object.oid : data.contractor.oid;
            data.contractor.ref = this.state.contractor.object;
            delete data.contractor.ref.oid;

            data.technician.oid = this.state.technician.object.oid ? this.state.technician.object.oid : data.technician.oid;
            data.technician.ref = this.state.technician.object;
            delete data.technician.ref.oid;

            this.props.edit_facility(data, this.state.posEditElement);
        }
    };
    handleSubmitDeleteFacility = (id, pos) => {
        this.props.delete_facility(id, pos);
    };
    handleChangeIdFacility = event => {
        event.preventDefault();
        this.setState({id: event.target.value});
    };
    handleChangeNameFacility = event => {
        event.preventDefault();
        this.setState({name: event.target.value});
    };
    handleChangeContractorFacility = (contractor) => {
        this.setState({contractor: contractor});
    };
    handleChangeTechnicianFacility = (technician) => {
        this.setState({technician: technician});
    };
    handleChangeLocationFacility = (location) => {
        this.setState({location: location});
    };
    handleChangeOrder = (order) => {
        this.setState({order: order});
    };
    handleChangeAddressFacility = event => {
        event.preventDefault();
        this.setState({address: event.target.value});
    };

    getLocationOption = (list) => {
        list = list.map(location => {
            return {
                id: location.oid,
                value: location.name,
                label: location.name,
                object: location
            };
        });

        list.unshift({id: 0, value: '', label: 'Выберите локацию', object: {oid: 0}});
        return list;
    };
    getContractorOption = (list) => {
        list = list.map(contractor => {
            return {
                id: contractor.oid,
                value: contractor.name,
                label: contractor.name,
                object: contractor
            };
        });
        list.unshift({id: 0, value: '', label: 'Выберите контрагент', object: {oid: 0}});
        return list;
    };
    getTechnicianOption = (list) => {
        list = list.map(technician => {
            return {
                id: technician.oid,
                value: getFIO((technician.user || {}).ref),
                label: getFIO((technician.user || {}).ref),
                object: technician
            };
        });
        list.unshift({id: 0, value: '', label: 'Выберите техника', object: {oid: 0}});
        return list;
    };

    getOrderOption = (list) => {
        list = list.map(order => {
            return {
                id: order.oid,
                value: order.name,
                label: order.name,
                object: order
            };
        });

        list.unshift({id: 0, value: '', label: 'Выберите регламент'});
        return list;
    };

    render() {
        const list_facility = Object.values(this.props.search_list_facility);
        const arrow = this.props.sortUp_facility ? <i className="fas fa-angle-down"> </i> :
            <i className="fas fa-angle-up"> </i>;

        /*Опции для выбора*/
        const locationOptions = this.getLocationOption(this.props.list_locations);
        const contractorOptions = this.getContractorOption(this.props.list_contractor);
        const technicianOptions = this.getTechnicianOption(this.props.list_technicians);

        return (
            <Fragment>
                <div className="row">
                    <form className="col-sm-8 col-xl-10 mt-3">
                        <input className="form-control" type="search" placeholder="Поиск по объектам"
                               aria-label="Search" onChange={this.search_facility}/>
                    </form>
                    <div className="col-sm-4 col-xl-2 mt-3">
                        <button className="btn btn-outline-danger col-12" data-toggle="modal"
                                data-target="#interactFacility"
                                onClick={this.handleSelectAddFacility}>Добавить объект
                        </button>
                    </div>
                </div>
                <table className="table mt-3">
                    <thead className="thead-light">
                    <tr className="d-flex">

                        <th className="col-1">id</th>
                        <th className="col-1 sort-button"
                            onClick={this.props.reverse_list_facility}>Название {arrow}</th>
                        <th className="col-2">Контрагент</th>
                        <th className="col-2">Локация/Адрес объекта</th>
                        <th className="col-3">Регламенты</th>
                        <th className="col-1">Техник</th>
                        <th className="col-1">Статус</th>
                        <th className="col-1"/>
                    </tr>
                    </thead>
                    <tbody>
                    {list_facility.map((facility, i) => {

                        /*Текущие значения полей при редактировании*/
                        let currentLocationOption = facility.zone.ref ? {
                            id: facility.zone.oid,
                            value: facility.zone.ref.name,
                            label: facility.zone.ref.name,
                            object: Object.assign(facility.zone.ref, facility.zone.oid)
                        } : {id: 0, value: '', label: 'Введите локацию', object: {oid: 0}};
                        let currentContractorOption = facility.contractor.ref ? {
                            id: facility.contractor.oid,
                            value: facility.contractor.ref.name,
                            label: facility.contractor.ref.name,
                            object: Object.assign(facility.contractor.ref, facility.contractor.oid)
                        } : {id: 0, value: '', label: 'Введите контрагент', object: {oid: 0}};
                        let currentTechnicianOption = facility.technician.ref ? {
                            id: facility.technician.oid,
                            value: getFIO(facility.technician.ref.user.ref),
                            label: getFIO(facility.technician.ref.user.ref),
                            object: Object.assign(facility.technician.ref, facility.technician.oid)
                        } : {id: 0, value: '', label: 'Введите техника', object: {oid: 0}};

                        const street = (facility.address || {}).street;
                        const home = (facility.address || {}).home;
                        const office = (facility.address || {}).office === "" ? "" : `оф. ${(facility.address || {}).office}`;
                        const technician = getFIO((((facility.technician || {}).ref || {}).user || {}).ref);
                        const location = ((facility.zone || {}).ref || {}).name;
                        const contractorName = ((facility.contractor || {}).ref || {}).name;
                        const contractorINN = ((facility.contractor || {}).ref || {}).INN;
                        return (
                            <tr key={facility.oid} className="d-flex">
                                <td className="col-1">{facility.identifier}</td>
                                <td className="col-1">{facility.name}</td>
                                <td className="col-2"><p>{contractorName}</p>
                                    <p>{contractorINN ? `ИНН: ${contractorINN}` : ""}</p></td>
                                <td className="col-2"><p>{location}</p><p>{street} {home} {office}</p></td>
                                <td className="col-3">
                                    <div className="row">
                                        <p className="col-8">Регламентных
                                            работ: {facility.order.reduce((sum, current) => {
                                                return sum + current.ref.jobs.length;
                                            }, 0)}
                                        </p>
                                        <div className="col-4">
                                            <button className="btn btn-danger btn-sm" data-toggle="modal"
                                                    data-target="#interactFacility"
                                                    onClick={this.handleSelectAddFacility}>Добавить
                                            </button>
                                        </div>

                                    </div>

                                    {facility.order.map(order => {
                                        return (
                                            <div key={order.oid} className="row">
                                                <p className="col-8">
                                                    {order.ref.order.ref.name}
                                                </p>
                                                <div className="col-4 text-right">
                                                    <button type="button" className="btn delete_button"
                                                            data-toggle="modal"
                                                            data-target="#deleteLocation"
                                                            onClick={() => this.handleSubmitDeleteFacility(facility.oid, i)}>
                                                        <i className="fas fa-times"/>
                                                    </button>
                                                </div>
                                            </div>
                                        )
                                    })}
                                </td>
                                <td className="col-1">{technician}</td>
                                <td className="col-1">{facilityState[facility.facilityState]}</td>
                                <td className="col-1 text-right">
                                    <button className="font-awesome-button" data-toggle="modal"
                                            data-target="#interactFacility"
                                            onClick={() => this.handleSelectEditFacility(i, facility.identifier, facility.name, currentContractorOption, `${street} ${home} ${office}`, currentLocationOption, currentTechnicianOption)}>
                                        <i className="fas fa-pencil-alt"/>
                                    </button>
                                    <button type="button" className="btn delete_button"
                                            data-toggle="modal"
                                            data-target="#deleteLocation"
                                            onClick={() => this.handleSubmitDeleteFacility(facility.oid, i)}>
                                        <i className="fas fa-trash-alt"/>
                                    </button>
                                </td>
                            </tr>
                        )
                    })}
                    </tbody>
                </table>

                {/*Модальное окно добавления/редактирования объекта*/}

                <div id="interactFacility" className="modal fade" role="dialog">
                    <div className="modal-dialog">
                        <div className="modal-content">
                            <form id="interactFacility"
                                  onSubmit={this.state.popupState === "add" ? this.handleSubmitAddFacility : this.handleSubmitEditFacility}>
                                <div className="modal-header">
                                    <h4 className="modal-title">{this.state.popupState === "add" ? 'Создание' : 'Редактирование'} объекта</h4>
                                    <button type="button" className="close" data-dismiss="modal">&times;</button>
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <label htmlFor="interactFacilityId">Идентификатор</label>
                                    <input className="form-control" id="interactFacilityId" type="text"
                                           placeholder="Введите идентификатор объекта"
                                           onChange={this.handleChangeIdFacility} required value={this.state.id}/>
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <label htmlFor="interactFacilityName">Название</label>
                                    <input className="form-control" id="interactFacilityName" type="text"
                                           placeholder="Введите название объекта"
                                           onChange={this.handleChangeNameFacility} required value={this.state.name}/>
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <p>Контрагент</p>
                                    <Select
                                        value={this.state.contractor}
                                        onChange={this.handleChangeContractorFacility}
                                        options={contractorOptions}
                                        placeholder={"Выберите контрагент"}
                                        className={this.state.contractor.id === 0 ? "multiselect_required" : "multiselect"}
                                    />
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <p>Локация</p>
                                    <Select
                                        value={this.state.location}
                                        onChange={this.handleChangeLocationFacility}
                                        options={locationOptions}
                                        placeholder={"Выберите локацию"}
                                        className={this.state.location.id === 0 ? "multiselect_required" : "multiselect"}
                                    />
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <p>Техник</p>
                                    <Select
                                        value={this.state.technician}
                                        onChange={this.handleChangeTechnicianFacility}
                                        options={technicianOptions}
                                        placeholder={"Выберите техника"}
                                        className={this.state.technician.id === 0 ? "multiselect_required" : "multiselect"}
                                    />
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <label htmlFor="interactFacilityAddress">Адрес</label>
                                    <input className="form-control" id="interactFacilityAddress" type="text"
                                           placeholder="Введите адрес"
                                           onChange={this.handleChangeAddressFacility}
                                           value={this.state.address}/>
                                </div>
                                <div className="modal-footer">
                                    <button type="submit" className="btn btn-outline-danger">
                                        {this.state.popupState === "add" ? 'Добавить' : 'Изменить'}
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    }
}

const mapStateToProps = ({listFacility, listLocations, listTechnicians, listOrder}) => ({
    list_facility: listFacility.list_facility,
    search_list_facility: listFacility.search_list_facility,
    sortUp_facility: listFacility.sortUp_facility,
    list_contractor: listFacility.list_contractor,
    list_locations: listLocations.list_locations,
    list_technicians: listTechnicians.list_technicians,
    list_order: listOrder.list_order
});

const mapDispatchToProps = dispatch =>
    bindActionCreators(
        {
            get_list_facility,
            get_search_list_facility,
            reverse_list_facility,
            add_facility,
            edit_facility,
            delete_facility,
            get_list_order,
            get_list_locations,
            get_list_contractor,
            add_technician_to_facility,
            get_list_technicians
        },
        dispatch
    );

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Facility);