import React, {Component, Fragment} from 'react'

import Timeline from 'react-calendar-timeline'
import 'react-calendar-timeline/lib/Timeline.css'
import moment from 'moment';
import 'moment/locale/ru';

import '../index.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.js';
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {
    get_list_maintenance,
    add_maintenance,
    delete_maintenance,
    close_maintenance,
    retry_maintenance,
    move_maintenance_to_technician,
    resize_maintenance_to_technician,
    get_list_shifts,
    add_shift
} from './ducks';
import {get_list_technicians} from '../technicians/ducks';
import {get_list_facility} from '../facility/ducks';
import {getFIO, maintenanceState, maintenanceType} from "../../commonComponents/Const";
import Select from "react-select";
import $ from "jquery";

const keys = {
    groupIdKey: "id",
    groupTitleKey: "title",
    groupRightTitleKey: "rightTitle",
    itemIdKey: "id",
    itemTitleKey: "title",
    itemDivTitleKey: "title",
    itemGroupKey: "group",
    itemTimeStartKey: "start",
    itemTimeEndKey: "end",
    groupLabelKey: "title"
};

class Calendar extends Component {
    constructor(props) {
        super(props);
        moment.locale('ru');

        const defaultTimeStart = moment()
            .startOf("day")
            .add(8, "hour")
            .toDate();
        const defaultTimeEnd = moment()
            .startOf("day")
            .add(20, "hour")
            .toDate();

        this.state = {
            groups: [],
            items: [],
            defaultTimeStart,
            defaultTimeEnd,
            facility: "",
            footerName: "",
            additionShift: {},
            editedMaintenance: 0,
            maintenanceType: 0
        };

        this.props.get_list_facility();

        let promises = [];
        promises.push(this.props.get_list_maintenance());
        promises.push(this.props.get_list_technicians());
        promises.push(this.props.get_list_shifts());
        Promise.all(promises)
            .then(() => {

                this.getCalendarData(this.props);

                console.log('Добавление данных в календарь выполнено')
            })
            .catch((e) => console.error('Добавление данных в календарь не выполнено', e));
    }

    componentWillReceiveProps(nextProps) {
        this.getCalendarData(nextProps);
    }

    getCalendarData = (props) => {
        const groups = [];
        props.list_technicians.map(tech => {
            groups.push({
                id: tech.oid,
                title: getFIO(tech.user.ref),
                object: tech
            })
        });
        groups.push({
            id: -1,
            title: "Не распределенные заявки",
        }, {
            id: -2,
            title: `${this.state.footerName} Объекты на месяц`
        });
        let timeForFooter = moment()
            .startOf("day")
            .add(8, "hour")
            .valueOf();
        let startTimeForFooter = moment()
            .startOf("day")
            .add(8, "hour")
            .valueOf();
        let endTimeForFooter = moment()
            .startOf("day")
            .add(17, "hour")
            .valueOf();

        const items = props.list_maintenance.map(maintenance => {
            let name;
            if (maintenance.facility.oid === 0) {
                name = "";
            } else {
                let nameTechnician = maintenance.facility.ref.technician.oid === 0 ? "" : getFIO(maintenance.facility.ref.technician.ref.user.ref) + '\n';
                let nameContractor = maintenance.facility.ref.contractor.oid === 0 ? "" : maintenance.facility.ref.contractor.ref.name;
                name = `${nameContractor}` +
                    `${maintenance.facility.ref.name}\n` +
                    `${maintenance.maintenanceState === 2 ? "" : "Дата: " + moment(maintenance.visitDate.timeInMS).format("YYYY-MM-DD HH:mm:ss")}\n` +
                    `Состояние заявки: ${maintenanceState[maintenance.maintenanceState]}\n` +
                    `Тип заявки: ${maintenanceType[maintenance.maintenanceType]}\n` +
                    `${maintenance.maintenanceState === 2 ? "" : "Ответственный за объект техник: " + nameTechnician}`;
            }

            let group, start, end, bgColor, canResize, canMove;

            switch (maintenance.maintenanceState) {
                case 2: { // нераспределенные заявки
                    group = -1;
                    start = timeForFooter;
                    end = timeForFooter + 60 * 60000;
                    bgColor = '#a7a7a7';
                    timeForFooter = timeForFooter > endTimeForFooter ? startTimeForFooter : end + 15 * 60000;
                    canResize = false;
                    break;
                }
                case 7:
                case 11:
                case 10:
                case 12: { // выполненные заявки, перенесенные, выполненные по частям и завершенные как есть
                    group = props.list_shifts.find(shift => shift.oid === maintenance.shift.oid);
                    group = group.technician.oid;

                    bgColor = '#67c873';
                    start = maintenance.visitDate.timeInMS;
                    end = maintenance.visitDate.timeInMS + maintenance.duration * 60000;
                    canResize = false;
                    canMove = false;
                    break;
                }
                case 6: { // заявки в обслуживании
                    group = props.list_shifts.find(shift => shift.oid === maintenance.shift.oid);
                    group = group.technician.oid;

                    bgColor = '#cd6bff';
                    start = maintenance.visitDate.timeInMS;
                    end = maintenance.visitDate.timeInMS + maintenance.duration * 60000;
                    canResize = false;
                    canMove = false;
                    break;
                }
                case 5: { // заявки с превышением времени
                    group = -1;
                    start = timeForFooter;
                    end = timeForFooter + 60 * 60000;
                    bgColor = '#e15151';
                    timeForFooter = timeForFooter > endTimeForFooter ? startTimeForFooter : end + 15 * 60000;
                    canResize = false;
                    break;
                }
                default: {
                    group = props.list_shifts.find(shift => shift.oid === maintenance.shift.oid);
                    group = group.technician.oid;
                    bgColor = '#74a5ff';
                    start = maintenance.visitDate.timeInMS;
                    end = maintenance.visitDate.timeInMS + maintenance.duration * 60000;
                    canResize = "both";
                    canMove = true;
                    break;
                }
            }

            return {
                id: maintenance.oid,
                group: group,
                title: name,
                start: start,
                end: end,
                color: 'black',
                selectedBgColor: '#fafa8a',
                bgColor: bgColor,
                object: maintenance,
                canResize: canResize,
                canMove: canMove
            }
        });
        this.setState({groups: groups, items: items});
    };

    groupRenderer = ({group}) => {
        return (
            <div className="custom-group pl-2 pr-2" id={group.id}
                 onClick={() => this.handleShowTechnicianFooter(group.id, group.title)}>
                <span className="title">{group.title}</span>
            </div>
        )
    };

    itemRenderer = ({item, itemContext, getItemProps, getResizeProps}) => {
        const {left: leftResizeProps, right: rightResizeProps} = getResizeProps();
        const backgroundColor = itemContext.selected ? item.selectedBgColor : item.bgColor;
        const borderColor = item.color;
        return (
            <div
                {...getItemProps({
                    style: {
                        backgroundColor,
                        color: item.color,
                        borderColor,
                        borderStyle: "solid",
                        borderWidth: 1,
                        borderRadius: 3,
                        borderLeftWidth: itemContext.selected ? 3 : 1,
                        borderRightWidth: itemContext.selected ? 3 : 1
                    }
                })}
            >
                {itemContext.useResizeHandle ? <div {...leftResizeProps} /> : null}

                <div
                    style={{
                        height: itemContext.dimensions.height,
                        overflow: "hidden",
                        paddingLeft: 3,
                        textOverflow: "ellipsis",
                        whiteSpace: "nowrap"
                    }}
                >
                    {itemContext.title}
                </div>

                {itemContext.useResizeHandle ? <div {...rightResizeProps} /> : null}
            </div>
        );
    };

    handleShowTechnicianFooter = (id, name) => {
        this.setState({footerName: name});
    };

    handleItemMove = (itemId, dragTime, newGroupOrder) => {
        const {items, groups} = this.state;

        const group = groups[newGroupOrder];
        let itemObject = 0;

        this.setState({
            items: items.map(item => {
                if (item.id === itemId) {
                    itemObject = item.object;
                    return Object.assign({}, item, {
                        start: dragTime,
                        end: dragTime + (item.end - item.start),
                        group: group.id
                    })
                } else {
                    return item;
                }
            })
        });

        const dayTime = moment(dragTime)
            .startOf("day")
            .valueOf();

        this.props.move_maintenance_to_technician(groups[newGroupOrder].id, dayTime, dragTime, itemObject);

        console.log("Moved", itemId, dragTime, newGroupOrder);
    };

    handleItemResize = (itemId, time, edge) => {
        const {items} = this.state;
        let duration = 0;

        this.setState({
            items: items.map(item => {
                if (item.id === itemId) {
                    duration = edge === "left" ? (item.end - time) / 60000 : (time - item.start) / 60000;

                    this.props.resize_maintenance_to_technician(item.object, duration, edge === "left" ? time : item.start);

                    return Object.assign({}, item, {
                        start: edge === "left" ? time : item.start,
                        end: edge === "left" ? item.end : time,
                    });

                } else {
                    return item;
                }
            })
        });

        console.log("Resized", itemId, time, edge, items);
    };
    handleCanvasContextMenu = (group, time) => {
        $(function () {
            $('#interactShift').modal('toggle');
        });
        time = moment(time)
            .startOf("day")
            .valueOf();
        this.setState({additionShift: {group: group, time: time}});
    };

    handleItemContextMenu = (itemId) => {
        $(function () {
            $('#editMaintenance').modal('toggle');
        });
        this.setState({editedMaintenance: itemId});
    };
    getFacilityOption = (list) => {
        list = list.map(facility => {
            return {
                id: facility.oid,
                value: facility.name,
                label: facility.name,
                object: facility
            };
        });

        list.unshift({id: 0, value: '', label: 'Выберите объект'});
        return list;
    };

    handleChangeFacility = (facility) => {
        this.setState({facility: facility});
    };

    handleSubmitAddMaintenance = event => {
        event.preventDefault();
        this.props.add_maintenance(this.state.facility, this.state.maintenanceType);
    };

    handleSubmitAddShift = event => {
        event.preventDefault();
        this.props.add_shift(this.state.additionShift);
    };

    handleDeleteMaintenance = event => {
        event.preventDefault();
        this.props.delete_maintenance(this.state.editedMaintenance);
    };

    handleChangeMaintenanceType = e => {
        const {value} = e.target;

        this.setState({
            maintenanceType: +value
        });
    };

    handleCloseMaintenance = event => {
        event.preventDefault();
        this.props.close_maintenance(this.state.editedMaintenance);
    };

    handleRetryMaintenance = event => {
        event.preventDefault();
        this.props.retry_maintenance(this.state.editedMaintenance);
    };

    render() {
        const {groups, items, defaultTimeStart, defaultTimeEnd} = this.state;

        const options = this.getFacilityOption(this.props.list_facility);

        let editedItem = this.state.items.find(item => item.id === this.state.editedMaintenance);

        return (
            <Fragment>
                <div className="row mt-3 mb-3 float-right">
                    <button className="btn btn-outline-danger mr-3" data-toggle="modal"
                            data-target="#interactMaintenance">Добавить заявку
                    </button>
                </div>

                <Timeline
                    groups={groups}
                    items={items}
                    keys={keys}
                    fullUpdate
                    itemTouchSendsClick={false}
                    stackItems
                    itemHeightRatio={0.75}
                    canMove={true}
                    canResize={"both"}
                    defaultTimeStart={defaultTimeStart}
                    defaultTimeEnd={defaultTimeEnd}
                    onItemMove={this.handleItemMove}
                    onItemResize={this.handleItemResize}
                    minZoom={60 * 60 * 1000 * 24}
                    maxZoom={30.43 * 86400 * 1000}
                    sidebarWidth={300}
                    itemRenderer={this.itemRenderer}
                    groupRenderer={this.groupRenderer}
                    onCanvasContextMenu={this.handleCanvasContextMenu}
                    onItemContextMenu={this.handleItemContextMenu}
                />

                {/*Модальное окно добавления заявки*/}
                <div id="interactMaintenance" className="modal fade" role="dialog">
                    <div className="modal-dialog">
                        <div className="modal-content">
                            <form id="interactMaintenance" onSubmit={this.handleSubmitAddMaintenance}>
                                <div className="modal-header">
                                    <h4 className="modal-title">Создание заявки</h4>
                                    <button type="button" className="close" data-dismiss="modal">&times;</button>
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <p>Обслуживаемый объект</p>
                                    <Select
                                        value={this.state.facility}
                                        onChange={this.handleChangeFacility}
                                        options={options}
                                        placeholder={"Выберите объект"}
                                        className={"multiselect"}
                                    />
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio"
                                               id="once_maintenance_radio" name="maintenance_state" value="2"
                                               onChange={this.handleChangeMaintenanceType}/>
                                        <label className="form-check-label" htmlFor="once_maintenance_radio">
                                            {maintenanceType[2]}
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio"
                                               id="to_maintenance_radio" name="maintenance_state" value="1"
                                               onChange={this.handleChangeMaintenanceType}/>
                                        <label className="form-check-label" htmlFor="to_maintenance_radio">
                                            {maintenanceType[1]}
                                        </label>
                                    </div>
                                </div>

                                <div className="modal-footer">
                                    <button type="submit" className="btn btn-outline-danger">Добавить</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>

                {/*Модальное окно добавления смены*/}
                <div id="interactShift" className="modal fade" role="dialog">
                    <div className="modal-dialog">
                        <div className="modal-content">
                            <form id="interactShift" onSubmit={this.handleSubmitAddShift}>
                                <div className="modal-header">
                                    <h4 className="modal-title">Создание смены</h4>
                                    <button type="button" className="close" data-dismiss="modal">&times;</button>
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <p>Хотите создать смену для
                                        техника {this.state.groups.find(group => group.id === this.state.additionShift.group) && this.state.groups.find(group => group.id === this.state.additionShift.group).title} на
                                        текущий день?</p>
                                </div>
                                <div className="modal-footer">
                                    <button type="submit" className="btn btn-outline-danger">Создать</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>

                {/*Модальное окно  редактирования заявки*/}
                <div id="editMaintenance" className="modal fade" role="dialog">
                    <div className="modal-dialog">
                        <div className="modal-content">
                            <form id="editMaintenance">
                                <div className="modal-header">
                                    <h4 className="modal-title">Редактирование заявки</h4>
                                    <button type="button" className="close" data-dismiss="modal">&times;</button>
                                </div>
                                <div className="modal-body pt-2 pb-2">
                                    <p>{editedItem && editedItem.title}</p>
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-outline-danger"
                                            onClick={this.handleDeleteMaintenance}>Удалить
                                    </button>
                                    <button type="button" className="btn btn-outline-danger"
                                            onClick={this.handleCloseMaintenance}>Закрыть досрочно
                                    </button>
                                    <button type="button" className="btn btn-outline-danger"
                                            onClick={this.handleRetryMaintenance}>Перенести заявку
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}

const mapStateToProps = ({listMaintenance, listTechnicians, listFacility, listOrder}) => ({
    list_maintenance: listMaintenance.list_maintenance,
    list_technicians: listTechnicians.list_technicians,
    list_facility: listFacility.list_facility,
    list_shifts: listMaintenance.list_shifts,
    list_order: listOrder.list_order
});

const mapDispatchToProps = dispatch =>
    bindActionCreators(
        {
            get_list_maintenance,
            get_list_technicians,
            get_list_facility,
            add_maintenance,
            close_maintenance,
            delete_maintenance,
            retry_maintenance,
            move_maintenance_to_technician,
            resize_maintenance_to_technician,
            get_list_shifts,
            add_shift
        },
        dispatch
    );

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Calendar);