import * as allConst from "../../commonComponents/Const";
import {ALL_ORDER_PATH} from "../../commonComponents/Const";
import {IP_HOST} from "../../commonComponents/Const";
import {ADD_ORDER_PATH} from "../../commonComponents/Const";
import $ from "jquery";
import {UPDATE_ORDER_PATH} from "../../commonComponents/Const";
import {compare} from "../../commonComponents/Const";

export const GET_LIST_ORDER = 'GET_LIST_ORDER';
export const GET_SEARCH_LIST_ORDER = 'GET_SEARCH_LIST_ORDER';
export const ADD_ORDER = 'ADD_ORDER';
export const EDIT_ORDER = 'EDIT_ORDER';

const initialState = {
    list_order: [],
    search_list_order: [],
    sortUp_order: true
};

/*reducers*/
export default (state = initialState, action) => {
    switch (action.type) {
        case GET_LIST_ORDER:
            return {
                ...state,
                list_order: action.list_order,
                sortUp_order: true
            };
        case GET_SEARCH_LIST_ORDER:
            return {
                ...state,
                search_list_order: action.search_list_order
            };
        case ADD_ORDER:
            return {
                ...state,
                list_order: [...state.list_order, action.order].sort(compare),
                search_list_order: [...state.search_list_order, action.order].sort(compare)
            };
        case EDIT_ORDER:
            return {
                ...state,
                list_order: state.list_order.map(order => order.oid === action.order.oid ? action.order : order).sort(compare),
                search_list_order: state.search_list_order.map(order => order.oid === action.order.oid ? action.order : order).sort(compare)
            };
        default:
            return state
    }
}

/*actions*/
export const get_list_order = () => {
    return dispatch => {
        fetch(`${IP_HOST}${ALL_ORDER_PATH}`, {
            method: "GET",
            headers: {'SessionToken': allConst.getCurrentUser().sessionToken}
        }).then(function (response) {
            if (response.status === 401) {
                document.location.href = "/";
            }
            return response.json()
        }).then(data => {
            data = data.sort(compare);
            dispatch({
                type: GET_LIST_ORDER,
                list_order: data,
            });
            dispatch({
                type: GET_SEARCH_LIST_ORDER,
                search_list_order: data
            });
            console.log("Список регламентов получен \n", data);
        }).catch(function (error) {
            console.log('Список регламентов не получен \n', error.message);
        });
    }
};

export const get_search_list_order = (data) => {
    return dispatch => {
        dispatch({
            type: GET_SEARCH_LIST_ORDER,
            search_list_order: data
        });
    }
};

export const add_order = (name) => {
    let body = {
        name: name,
        jobs: []
    };
    return dispatch => {
        fetch(`${IP_HOST}${ADD_ORDER_PATH}`, {
            method: "POST",
            headers: {'SessionToken': allConst.getCurrentUser().sessionToken},
            body: JSON.stringify(body)
        }).then(function (response) {
            if (response.status === 401) {
                document.location.href = "/";
            }
            return response.json()
        }).then(data => {
            $(function () {
                $('#interactOrder').modal('toggle');
            });

            body.oid = data;

            dispatch({
                type: ADD_ORDER,
                order: body,
            });

            console.log("Регламент добавлен \n", data);
        }).catch(function (error) {
            console.log('Регламент не добавлен \n', error.message);
        });
    }
};

export const update_order = (name, object) => {

    object.name = name;
    return dispatch => {
        fetch(`${IP_HOST}${UPDATE_ORDER_PATH}`, {
            method: "POST",
            headers: {'SessionToken': allConst.getCurrentUser().sessionToken},
            body: JSON.stringify(object)
        }).then(function (response) {
            if (response.status === 401) {
                document.location.href = "/";
            }
            return response.json()
        }).then(data => {
            $(function () {
                $('#interactOrder').modal('toggle');
            });

            dispatch({
                type: EDIT_ORDER,
                order: object
            });

            console.log("Регламент изменен \n", data);
        }).catch(function (error) {
            console.log('Регламент не изменен \n', error.message);
        });
    }
};